import { Button } from 'primereact/button';
import { Menubar } from 'primereact/menubar';
import { PageRequestError } from 'components/PageRequestError';
import { ProgressSpinner } from 'primereact/progressspinner';
import { Title } from 'components/Title';
import KbmGuruEditPage from 'pages/kbm_guru/Edit';
import useApp from 'hooks/useApp';

import useViewPage from 'hooks/useViewPage';
const KbmGuruViewPage = (props) => {
		const app = useApp();
	const pageController = useViewPage(props);
	const { item, pageReady, loading, apiRequestError, deleteItem } = pageController;
	const pageExportFormats =  [
		'pdf'
	];
	function ActionButton(data){
		const items = [
		{
			label: "Edit",
			command: (event) => { app.openPageDialog(<KbmGuruEditPage isSubPage apiPath={`/kbm_guru/edit/${data.id_kehadiran_ajar}`} />, {closeBtn: true }) },
			icon: "pi pi-pencil"
		},
		{
			label: "Delete",
			command: (event) => { deleteItem(data.id_kehadiran_ajar) },
			icon: "pi pi-trash"
		}
	]
		return (<Menubar className="p-0 " model={items} />);
	}
	function PageFooter() {
		if (props.showFooter) {
			return (
				<div className="flex justify-content-between">
				</div>
			);
		}
	}
	if(loading){
		return (
			<div className="p-3 text-center">
				<ProgressSpinner style={{width:'50px', height:'50px'}} />
			</div>
		);
	}
	if(apiRequestError){
		return (
			<PageRequestError error={apiRequestError} />
		);
	}
	if(pageReady){
		return (
			<div>
<main id="KbmGuruViewPage" className="main-page">
    { (props.showHeader) && 
    <section className="page-section mb-3" >
        <div className="container">
            <div className="grid align-items-center">
                { !props.isSubPage && 
                <div className="col-fixed " >
                    <Button onClick={() => app.navigate(-1)} label=""  className="p-button p-button-text " icon="pi pi-arrow-left"  />
                </div>
                }
                <div className="col " >
                    <Title title="Kbm Guru Details"   titleClass="text-2xl text-primary font-bold" subTitleClass="text-sm text-500"      separator={false} />
                </div>
            </div>
        </div>
    </section>
    }
    <section className="page-section " >
        <div className="container">
            <div className="grid ">
                <div className="col comp-grid" >
                    <div >
                        {/*PageComponentStart*/}
                        <div className="mb-3 grid justify-content-start">
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Tanggal</div>
                                        <div className="font-bold">{ item.tanggal }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Nama</div>
                                        <div className="font-bold">{item.nama && <Button className="p-button-text" icon="pi pi-eye" label={item.tbl_guru_nama_guru} to={`/tbl_guru//${item.nama}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Bidang Studi</div>
                                        <div className="font-bold">{item.bidang_study && <Button className="p-button-text" icon="pi pi-eye" label={item.tbl_bidang_study_nama_bidang} to={`/tbl_bidang_study//${item.bidang_study}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Kelas</div>
                                        <div className="font-bold">{item.kelas && <Button className="p-button-text" icon="pi pi-eye" label={item.tbl_kelas_nama_kelas} to={`/tbl_kelas//${item.kelas}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Jurusan</div>
                                        <div className="font-bold">{ item.jurusan }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Jam Ke</div>
                                        <div className="font-bold">{item.jam_ke && <Button className="p-button-text" icon="pi pi-eye" label={item.tbl_jam_nama_jam} to={`/tbl_jam//${item.jam_ke}`} /> }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Kehadiran</div>
                                        <div className="font-bold">{ item.kehadiran }</div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-12 md:col-4">
                                <div className="flex gap-3 align-items-center card p-3">
                                    <div className="">
                                        <div className="text-400 font-medium mb-1">Keterangan</div>
                                        <div className="font-bold">{ item.keterangan }</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="flex justify-content-between">
                            {props.exportButton && <Button icon="pi pi-print" className="mx-xs" title="Export" /> }
                            <div className="flex justify-content-start">
                                {ActionButton(item)}
                            </div>
                        </div>
                        {/*PageComponentEnd*/}
                    </div>
                </div>
            </div>
        </div>
    </section>
</main>
				<PageFooter />
			</div>
		);
	}
}
KbmGuruViewPage.defaultProps = {
	id: null,
	primaryKey: 'id_kehadiran_ajar',
	pageName: 'kbm_guru',
	apiPath: 'kbm_guru/view',
	routeName: 'kbm_guruview',
	msgBeforeDelete: "Yakin Menghapus Data Ini?",
	msgTitle: 'Delete Record',
	msgAfterDelete: "Data Berhasil Di Hapus !",
	showHeader: true,
	showFooter: true,
	exportButton: true,
	isSubPage: false,
}
export default KbmGuruViewPage;
